@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro&display=swap);
body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
        "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}


.page_decToBin {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f29188;
  margin: 0px;
  min-height: 100vh;
}

.page_decToBin .box {
  width: 60%;
  height: 30%;
  border: 2px solid #f20c49;
  border-bottom: 8px solid #f20c49;
  border-right: 8px solid #f20c49;
  border-radius: 7px;
  border-bottom-right-radius: 4px;
  background-color: #f2c48d;
  padding: 5px;
  box-shadow: 0px 4px 8px 0px rgba(242, 12, 73, 0.5);
}

.page_decToBin .box div {
  display: flex;
  margin: 10px;
  align-items: center;
}

.page_decToBin h3 {
  color: #f20c49;
  margin: 0 15px 0 0;
}

.page_decToBin input[type="text"] {
  width: 59%;
  height: 40px;
  border-radius: 5px;
  padding: 0 5px;
  font-size: 18px;
  border: 2px solid #3bbfbf;
  border-bottom: 5px solid #3bbfbf;
  border-right: 5px solid #3bbfbf;
  background-color: #9bf2d2;
  color: #f20c49;
  outline: none;
}

.page_decToBin input[type="text"]::-webkit-input-placeholder {
  color: #f20c49;
}

.page_decToBin input[type="text"]:-ms-input-placeholder {
  color: #f20c49;
}

.page_decToBin input[type="text"]::-ms-input-placeholder {
  color: #f20c49;
}

.page_decToBin input[type="text"]::placeholder {
  color: #f20c49;
}

.page_decToBin .resultado {
  width: 80%;
  height: 40px;
  border-radius: 5px;
  padding: 0 5px;
  font-size: 20px;
  border: 2px solid #3bbfbf;
  border-bottom: 5px solid #3bbfbf;
  border-right: 5px solid #3bbfbf;
  background-color: #9bf2d2;
  color: #f20c49;
}
