.page_stopwatch {
  background-color: #262020;
  color: #f2e9e9;
  min-height: 100vh;
}

.page_stopwatch .block {
  padding: 1rem 8rem;
}

.page_stopwatch .block .clockProperties {
  justify-content: center;
  align-items: center;
  border-bottom: 2px solid #594e4d;
}

.page_stopwatch .block .clockProperties button {
  font-size: 25px;
  margin: 5px 5px 10px 0;
  padding: 4px;
  border: 0px;
  border-bottom: 2px solid #f2e9e9;
  background-color: #262020;
  color: #f2e9e9;
  transition: all 0.2s;
  outline: none;
}

.page_stopwatch .block .clockProperties button:hover {
  background-color: #f2e9e9;
  color: #262020;
  cursor: pointer;
}

.page_stopwatch .block .clock {
  display: flex;
  align-items: center;
  font-size: 40px;
  margin-right: 20px;
}

.page_stopwatch .block #laps {
  border-top: 2px solid #594e4d;
  max-height: 20rem;
  overflow-y: scroll;
}

.page_stopwatch .block #laps::-webkit-scrollbar {
  width: 12px;
}

.page_stopwatch .block #laps::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.page_stopwatch .block #laps::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}

.page_stopwatch .block #laps div {
  border-bottom: 2px solid #594e4d;
}

.page_stopwatch .block #laps div h2 {
  margin-left: 20px;
}
/*# sourceMappingURL=styles.css.map */
.page_borderRadius {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0px;
  min-height: 100vh;
  background: #0f0526;
  font-family: "Source Code Pro", monospace;
  /* The slider handle (use -webkit- (Chrome, Opera, Safari, Edge) and -moz- (Firefox) to override default look) */
}

.page_borderRadius .border {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 30%;
  border: 3px solid #51818c;
  color: #51818c;
  padding: 8rem 5rem;
  background-color: #1c0c40;
}

.page_borderRadius .border input[type="button"] {
  margin-top: 1rem;
  background-color: #1c0c40;
  border: solid 2px #77a6a6;
  padding: 5px;
  color: #77a6a6;
  cursor: pointer;
}

.page_borderRadius .border input[type="button"]:hover {
  background-color: #77a6a6;
  color: #1c0c40;
}

.page_borderRadius #typeinp {
  -webkit-appearance: none;
  /* Override default CSS styles */
  -webkit-transition: 0.2s;
  /* 0.2 seconds transition on hover */
  outline-color: #77a6a6;
  background-color: #f2f2f2;
}

.page_borderRadius #typeinp::-webkit-slider-thumb {
  -webkit-appearance: none;
  /* Override default look */
  appearance: none;
  width: 15px;
  /* Set a specific slider handle width */
  height: 15px;
  /* Slider handle height */
  background: #77a6a6;
  /* Green background */
  cursor: pointer;
  /* Cursor on hover */
}

.page_borderRadius #typeinp::-moz-range-thumb {
  width: 15px;
  /* Set a specific slider handle width */
  height: 15px;
  /* Slider handle height */
  border-radius: 0;
  border: 2px solid #77a6a6;
  background: #77a6a6;
  /* Green background */
  cursor: pointer;
  /* Cursor on hover */
}

.page_borderRadius #textCopy {
  display: none;
}
