header {
  display: flex;
  position: fixed;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.4);
}

header .navbar {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 3rem;
  transition: all 0.2s;
}

header .navbar p {
  color: white;
  font-size: 1.5rem;
  margin: 0.3rem;
  transform: scaleX(1.3);
  cursor: default;
}

header .navbar a {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 3rem;
  width: 0;
  overflow: hidden;
  transition: all 0.2s;
  white-space: nowrap;
  text-decoration: none;
  color: white;
}

header .navbar a:hover {
  background-color: rgba(0, 0, 0, 0.9);
  box-shadow: 0px 0px 6px 5px solid rgba(0, 0, 0, 0.9);
}

header .navbar a:last-child {
  margin-top: auto;
  width: 100%;
}

header .navbar a:last-child svg {
  -webkit-filter: invert(100%);
          filter: invert(100%);
}

header .navbar:hover {
  width: 10rem;
  background-color: rgba(0, 0, 0, 0.7);
}

header .navbar:hover a {
  width: 100%;
}
